import React, {Component} from 'react';

class Register extends Component {
	constructor(props){
		super(props);
		this.state = {
			nameRegister: '',
			emailRegister: '',
			passwordRegister: ''
		}
	}

	onEmailChange = (event) => {
		this.setState({emailRegister:event.target.value});
	}

	onNameChange = (event) => {
		this.setState({nameRegister:event.target.value});
	}

	onPasswordChange = (event) => {
		this.setState({passwordRegister:event.target.value});
	}

	onSubmitRegister = () => {
  		console.log('onSubmitRegister')
		fetch('https://agile-lake-77488.herokuapp.com/register',{
			method:'post',
			headers: {'Content-Type':'application/json'},
			body: JSON.stringify({
				name: this.state.nameRegister,
				email: this.state.emailRegister,
				password: this.state.passwordRegister
			})
		}).then(response => response.json())
		.then(res => {
			console.log(res)
  			if(res!='register error'){
				this.props.loadUser(res)
				this.props.onRouteChange('home')
  			}
  		})
	}

	render(){
		return (
			<article className="br3 shadow-5 ba dark-gray b--black-10 mv6 w-100 w-50-m w-33-l center">
				<main className="pa1 black-80">
				  <div className="measure ">
				    <fieldset id="sign_up" className="ba b--transparent ph0 mh0">
				      <legend className="f4 fw6 ph0 mh0">Register</legend>
				      <div className="mt3">
				        <label className="db fw6 lh-copy f6" htmlFor="name">Name</label>
				        <input onChange= {this.onNameChange} 
				        className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" type="text" name="name"  id="name"/>
				      </div>
				      <div className="mt3">
				        <label className="db fw6 lh-copy f6" htmlFor="email-address">Email</label>
				        <input onChange= {this.onEmailChange} 
				        className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" type="email" name="email-address"  id="email-address"/>
				      </div>
				      <div className="mv3">
				        <label className="db fw6 lh-copy f6" htmlFor="password">Password</label>
				        <input onChange= {this.onPasswordChange} 
				        className="b pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" type="password" name="password"  id="password"/>
				      </div>
				    </fieldset>
				    <div className="">
				      <input onClick={this.onSubmitRegister} className="b ph3 pv2 input-reset ba b--black bg-transparent grow pointer f6 dib" type="submit" value="Sign in"/>
				    </div>
				  </div>
				</main>
			</article>
		)
	}
}


export default Register;